import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, InputNumber } from 'antd';
import { getPolicyObjectById, updatePolicyObject, createPolicyObject } from 'services/policyObject';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    label: '',
    decreasePct: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormPolicyObjectsPage' })
class FormPolicyObjectsPage extends Component {
  state = {
    policyOjectId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ policyOjectId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getPolicyObjectById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { policyOjectId } = this.state;
    try {
      if (!policyOjectId) {
        await createPolicyObject(values);
      } else {
        await updatePolicyObject({ id: policyOjectId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { policyOjectId } = this.state;
    return (
      <Card title={policyOjectId ? 'Cập nhật đối tượng chính sách' : 'Thêm đối tượng chính sách'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên đối tượng" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Miễn giảm (%)" hasFeedback>
              {getFieldDecorator('decreasePct', {
                initialValue: initialValue.decreasePct,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber min={0} max={99} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPolicyObjectsPage {...props} />
  </Page>
));



